* {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
}

body {
  margin: 0;
  background-color:#0c192c;
}


.container{
  position: relative;
  overflow:hidden;  
  width:100%;
  height:100vh; 
  }
  
  .bubbles{
  
  position:relative;
  display:flex;
  
  }
  
  .bubbles span{
  position:relative;
  z-index: -10;
  width:30px;
  height:30px;
  background:#4fc3dc;
  margin:0 4px;
  border-radius: 50%;
  box-shadow: 0 0 0 10px #4fc3dc44 ,
  0 0 50px #4fc3dc,
  0 0 100px #4fc3dc;
  animation : animate 15s linear infinite;
  animation-duration:calc(125s / var(--i))
  }

.bubbles span:nth-child(even){
  background:#ff2d75;
  box-shadow: 0 0 0 10px #ff2d7544 ,
  0 0  50px #ff2d75,
  0 0  100px #ff2d75;

}


@keyframes animate{

0%{

transform: translateY(100vh) scale(0);
}
100%{

transform: translateY(-20vh) scale(1);

}

  }


